 */


<template>

  <div id="test">
    <input type="text" placeholder="请输入文字" v-model="msg" @keypress.enter="enter">

    <p>{{upper}}</p>
    <test-son msg2='11hi'>happ</test-son>
  </div>
</template>

<script>
  import TestSon from './TestSon'
  export default {
    components: {TestSon},
    name: 'test',
    data () {
      return {
        msg: '欢迎访问我的网页'
      }
    },
    methods: {
      enter () {
        alert(this.msg)
      }
    },
    computed: {
      upper () {
        return this.msg.toUpperCase()
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s
  }
  .fade-enter, .fade-leave-active {
    opacity: 0
  }
</style>



